body{
    font-family: 'Roboto Condensed', sans-serif;
}
.separator{
    border-bottom: 1px solid rgba(0,0,0,.1);
    margin: 20px 0px;
}
.cart{
    color: #fff;
    margin-top: 8px;
    float: right;
    text-decoration: none;
}

.cart:hover{
    color: #fff;
    text-decoration: none;
}
.badge {
    font-size: 14px;
    color: #fbd30e;
    background-color: #000;
    border-radius: 13px;
    margin-left: 10px;
}
.cart-icon{
    margin-right: 5px;
    display: inline-block;
}


.nav-wrapper{
    margin: 8px 0px;
    float: left;
    width: 100%;
}
.nav{
    background: #2874f0;
}
.logo{
    float: left;
}
.mobile{
    color: #fff;
}

.shop{
    color: #fbd30e;
    margin-left: 5px;
}

.logo-text{
    float: left;
}

.navitem {
    color: #fff;
    display: inline-block;
    margin: 11px 25px;
    text-decoration: none;
    border-bottom: 1px solid transparent;
}
.navitem:hover{
    color: #fff;
    text-decoration: none;
    border-bottom: 1px solid #fff;
}


@media only screen and (max-width: 600px) {
    .nav-wrapper{
        margin: 15px 30px;
        float: left;
        width: 89%;
    }

}
.products{
    padding-top: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.product-wrapper{
    text-align: center;
    position: relative;
    min-height: 210px;
}
.caption{
    margin-bottom: 40px;
}
.mobile-name{
    margin-top: 20px;
}
.product-img{
    max-width: 60%;
    max-height: 100%;
}
.mobile-price{
    color: #388e3c;
}
.billboard{
    margin-top: 25px;
}
.product-detail-wrapper{
    margin: 40px 0px;
    padding: 40px 0px;
    display: inline-block;
}
